import { useEffect, useState, useRef } from 'react';
import $ from 'jquery';

export const SOLTicker = () => {
    

    const [solPrice, setSolPrice] = useState(0);
    const solPriceSpan = document.getElementById("solPrice");
    useEffect(() => {
        const intervalId = setInterval(() => {         
            $.ajax({
                url: "https://api.binance.com/api/v3/ticker/price?symbol=SOLUSDT",
                dataType: "json",
                method: "GET",
                success: function(response) {
                    setSolPrice(response.price);
                    /*if(solPrice.toString() != response.price.toString()){                       
                        solPriceSpan?.classList.toggle("changePrice");                    
                    }*/
                }
              });
    
        }, 3000);
        return () => clearInterval(intervalId);
      }, [solPrice]);

    return parseFloat(solPrice.toString());
  }
