import React, { useEffect, useState, useRef } from "react";
import "react-step-progress-bar/styles.css";
// @ts-ignore
import { ProgressBar, Step } from "react-step-progress-bar";
import { SOLTicker } from "../SOLTicker";



const getCashbackSum = (prices, counter) => {
  let result = 0;
  prices.slice(0, counter).forEach(element => {
    result += parseInt(element.replace(",", ""));
});
return result;
}

const getCashbackSumFormatted = (prices, counter) => {
  let sum = getCashbackSum(prices, counter).toString();
  if(sum == "0"){
    return "0";
  }
  return sum.slice(0,-3) + "," + sum.slice(-3);

}

const getCashbackToSolString = (cashbackRate, solPrice) => {
  return cashbackRate + "$ &asymp; "+(parseInt(cashbackRate.replace(",", "")) / solPrice).toFixed(2)+" SOL";
}


const getStep = (cashbackEventCounter, cashbackRates, addText = "", preText = "") => {
  
  return <Step transition="scale">
          {({ accomplished, index }) => ( 
             <div className={"stepDiv " + (cashbackEventCounter == index ? "active" : "inactive")}>     
            <img
              style={{ filter: `grayscale(${accomplished ? 0 : 80}%)` }}
              width="30"
              src="img/schnabulator.png"
            />
            <p  
            className="stepText">{preText}{cashbackRates[index]}{addText}</p>
            </div>

          )}
      </Step>
}


const useProgress = (progressSteps) => {
  const [progress, setProgress] = useState(0);
  const [counter, setCounter] = useState(0);
  useEffect(() => {
    const intervalId = setInterval(() => {
      
      if(progress < 100){
        
        setProgress(progress => (progress + progressSteps) );
      }

      else if(counter < 4){
        setCounter(counter => counter+1);
      }
      else{
        setCounter(0);
        setProgress(0);
      }

    }, 1000);
    return () => clearInterval(intervalId);
  }, [progress, counter]);



  useEffect(() => {
    setProgress(progress);
  }, [progress]);

  return progress;
};

